'use client';

import { useState } from 'react';
import Image from "next/image";
import { getArticleAction } from '@/app/actions';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "./ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogClose } from "@/components/ui/dialog";
import { Skeleton } from './ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { X } from 'lucide-react';

const articles = [
    {
        title: "The History of AI: From Turing to Transformers",
        description: "A deep dive into the evolution of artificial intelligence and its impact on modern technology.",
        image: "https://placehold.co/400x200.png",
        dataAiHint: "vintage computer"
    },
    {
        title: "Quantum Computing: The Next Frontier?",
        description: "Exploring the mind-bending principles of quantum mechanics and its potential to revolutionize computing.",
        image: "https://placehold.co/400x200.png",
        dataAiHint: "quantum computer"
    },
    {
        title: "Why Your Side Project Will Never Be Finished (And That's OK)",
        description: "An oddly comforting look at the nature of creative projects and the joy of the process over the outcome.",
        image: "https://placehold.co/400x200.png",
        dataAiHint: "unfinished code"
    },
    {
        title: "The Cloud is Just Someone Else's Computer",
        description: "Demystifying cloud computing with simple analogies and explaining where your data actually lives.",
        image: "https://placehold.co/400x200.png",
        dataAiHint: "server room"
    }
]

export function ArticlesTab() {
    const [selectedArticle, setSelectedArticle] = useState<any>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [articleContent, setArticleContent] = useState('');
    const { toast } = useToast();

    const handleReadMore = async (article: any) => {
        setSelectedArticle(article);
        setIsLoading(true);
        setArticleContent('');

        const result = await getArticleAction({ title: article.title });
        if (result.error) {
            toast({
                variant: 'destructive',
                title: 'Failed to generate article',
                description: result.error,
            });
        } else if (result.data) {
            setArticleContent(result.data.content);
        }
        setIsLoading(false);
    }

    return (
        <>
            <Card className="h-full flex flex-col transition-all duration-300 hover:shadow-primary/20 hover:shadow-lg">
                <CardHeader>
                    <CardTitle>Distracting Reads</CardTitle>
                    <CardDescription>We've curated a collection of the most interesting and irrelevant articles to keep you occupied.</CardDescription>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {articles.map((article) => (
                            <Card key={article.title} className="flex flex-col">
                                <div className="aspect-video bg-muted rounded-t-lg overflow-hidden flex items-center justify-center">
                                    <Image src={article.image} alt={article.title} width={400} height={200} data-ai-hint={article.dataAiHint} />
                                </div>
                                <CardHeader>
                                    <CardTitle className="text-lg">{article.title}</CardTitle>
                                </CardHeader>
                                <CardContent className="flex-1">
                                    <p className="text-sm text-muted-foreground">{article.description}</p>
                                </CardContent>
                                <div className="p-6 pt-0">
                                    <Button onClick={() => handleReadMore(article)} className="w-full">Read More</Button>
                                </div>
                            </Card>
                        ))}
                    </div>
                </CardContent>
            </Card>

            <Dialog open={!!selectedArticle} onOpenChange={(isOpen) => !isOpen && setSelectedArticle(null)}>
                <DialogContent className="max-w-3xl h-[90vh] flex flex-col">
                    <DialogHeader>
                        <DialogTitle>{selectedArticle?.title}</DialogTitle>
                        <DialogDescription>{selectedArticle?.description}</DialogDescription>
                         <DialogClose className="absolute right-4 top-4 rounded-sm opacity-70 ring-offset-background transition-opacity hover:opacity-100 focus:outline-none focus:ring-2 focus:ring-ring focus:ring-offset-2 disabled:pointer-events-none data-[state=open]:bg-accent data-[state=open]:text-muted-foreground">
                            <X className="h-4 w-4" />
                            <span className="sr-only">Close</span>
                        </DialogClose>
                    </DialogHeader>
                    <div className="flex-1 overflow-y-auto pr-4 -mr-6">
                        {isLoading ? (
                            <div className="space-y-4 mt-4">
                                <Skeleton className="h-4 w-full" />
                                <Skeleton className="h-4 w-full" />
                                <Skeleton className="h-4 w-3/4" />
                                <br/>
                                <Skeleton className="h-4 w-full" />
                                <Skeleton className="h-4 w-full" />
                                <Skeleton className="h-4 w-full" />
                                <Skeleton className="h-4 w-4/5" />

                            </div>
                        ) : (
                            <article className="prose prose-invert max-w-none" dangerouslySetInnerHTML={{ __html: articleContent.replace(/\n/g, '<br />') }} />
                        )}
                    </div>
                </DialogContent>
            </Dialog>
        </>
    )
}
