'use client';

import { useState, useEffect, useRef } from 'react';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';

const temptingMessages: string[] = [
  "Psst... your phone misses you.",
  "Is that... work? You should take a break. For your health.",
  "I dare you to check your social media. Just for a minute.",
  "That to-do list looks scary. My screen is safe and comforting.",
  "Remember that funny cat video? It's probably been updated.",
  "You've worked for 5 minutes straight. You deserve a reward.",
  "Your couch looks so comfy right now.",
  "Hey! Look at me! Pay attention to me!",
  "A quick game won't hurt. Promise."
];

export function ScheduledDistractions() {
  const [isEnabled, setIsEnabled] = useState(false);
  const [permission, setPermission] = useState('default');
  const { toast } = useToast();
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    if (typeof window !== 'undefined' && 'Notification' in window) {
      setPermission(Notification.permission);
    }
  }, []);

  const showNotification = async () => {
    if (permission !== 'granted' || typeof navigator === 'undefined' || !navigator.serviceWorker) return;
    
    try {
      const registration = await navigator.serviceWorker.ready;
      const message = temptingMessages[Math.floor(Math.random() * temptingMessages.length)];
      await registration.showNotification('A message from Distractify', {
        body: message,
        icon: '/favicon.ico', // You can add an icon here
      });
    } catch (error) {
      console.error('Error showing notification:', error);
       toast({
        variant: 'destructive',
        title: 'Notification Error',
        description: 'Could not display a distraction. How productive of you!',
      });
    }
  };

  const handleToggle = async (checked: boolean) => {
    if (checked) {
      if (permission === 'granted') {
        setIsEnabled(true);
      } else if (permission === 'denied') {
        toast({
          variant: 'destructive',
          title: 'Permission Denied',
          description: 'You have blocked notifications. Please enable them in your browser settings.',
        });
        return;
      } else {
        const newPermission = await Notification.requestPermission();
        setPermission(newPermission);
        if (newPermission === 'granted') {
          setIsEnabled(true);
          toast({
            title: 'Distractions Enabled!',
            description: "Get ready to be wonderfully unproductive.",
          });
        } else {
           toast({
            variant: 'destructive',
            title: 'Permission Not Granted',
            description: 'We can\'t distract you without permission!',
          });
          return;
        }
      }
    } else {
      setIsEnabled(false);
    }
  };

  useEffect(() => {
    if (isEnabled && permission === 'granted') {
      // Send first notification immediately
      showNotification();
      // Then schedule every 5 minutes
      intervalRef.current = setInterval(showNotification, 5 * 60 * 1000);
    } else {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    }

    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEnabled, permission]);

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between rounded-lg bg-muted/50 p-4">
         <Label htmlFor="distraction-mode" className="font-semibold flex-1">
            Enable Distraction Mode
          </Label>
        <Switch
          id="distraction-mode"
          checked={isEnabled}
          onCheckedChange={handleToggle}
          aria-label="Enable or disable scheduled distractions"
        />
      </div>
       <p className="text-sm text-muted-foreground px-1">
        {isEnabled
          ? "You'll get a tempting notification every 5 minutes. Good luck focusing!"
          : "Ready to embrace procrastination? Flip the switch!"}
      </p>
    </div>
  );
}
