'use client';

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "./ui/button";
import { Puzzle, MousePointerClick, Cookie, BrainCircuit } from 'lucide-react';
import Image from "next/image";
import { motion, useAnimation } from "framer-motion";
import { cn } from '@/lib/utils';

const UselessButtonGame = () => {
    const controls = useAnimation();
    const [clicks, setClicks] = useState(0);

    const messages = [
        "Nope.", "Still nothing.", "Try again.", "Are you sure?", "That did something... or did it?", "Click me again!", "You're persistent.", "I'm just a button.", "Okay, okay, you win... this click.", "One more time?"
    ];

    const handleClick = () => {
        setClicks(c => c + 1);
        controls.start({
            x: [0, -5, 5, -5, 5, 0],
            rotate: [0, -2, 2, -2, 2, 0],
            transition: { duration: 0.3 }
        });
    }

    return (
        <div className="flex flex-col items-center justify-center gap-4 text-center">
            <motion.div animate={controls}>
                <Button onClick={handleClick} className="px-8 py-6 text-lg">
                    <MousePointerClick className="mr-2" />
                    Click Me
                </Button>
            </motion.div>
            <p className="text-sm text-muted-foreground h-5">
                {clicks > 0 ? messages[clicks % messages.length] : "There's a button. You can click it. That's it."}
            </p>
        </div>
    )
}

const CookieClickerGame = () => {
    const [cookies, setCookies] = useState(0);

    const handleClick = (e: React.MouseEvent<HTMLButtonElement>) => {
        setCookies(c => c + 1);

        const button = e.currentTarget;
        const rect = button.getBoundingClientRect();
        const x = e.clientX - rect.left - 10;
        const y = e.clientY - rect.top - 10;
        
        const plusOne = document.createElement('span');
        plusOne.innerText = '+1';
        plusOne.style.position = 'absolute';
        plusOne.style.left = `${x}px`;
        plusOne.style.top = `${y}px`;
        plusOne.style.pointerEvents = 'none';
        plusOne.style.userSelect = 'none';
        plusOne.className = 'animate-ping-slow text-primary font-bold';
        
        button.appendChild(plusOne);
        setTimeout(() => {
            plusOne.remove();
        }, 1000);
    }
    
    return (
        <div className="flex flex-col items-center justify-center gap-4 text-center">
            <Button onClick={handleClick} variant="ghost" className="w-32 h-32 rounded-full relative overflow-hidden">
                <Cookie className="w-24 h-24 text-amber-500"/>
            </Button>
            <p className="text-xl font-bold">{cookies} Cookies</p>
            <p className="text-sm text-muted-foreground">Click the cookie. Get more cookies. The ultimate idle game.</p>
        </div>
    )
}


