export function GamesTab() {
    const [activeGame, setActiveGame] = useState<string | null>(null);

    const GameComponent = games.find(g => g.id === activeGame)?.component;

    if (activeGame && GameComponent) {
        return (
             <Card className="h-full flex flex-col transition-all duration-300 hover:shadow-primary/20 hover:shadow-lg">
                <CardHeader>
                     <Button variant="ghost" onClick={() => setActiveGame(null)} className="self-start">
                        &larr; Back to Games
                    </Button>
                </CardHeader>
                <CardContent className="flex-1 flex items-center justify-center">
                   {GameComponent}
                </CardContent>
             </Card>
        )
    }

    return (
        <Card className="h-full flex flex-col transition-all duration-300 hover:shadow-primary/20 hover:shadow-lg">
            <CardHeader>
                <CardTitle>Game Zone</CardTitle>
                <CardDescription>Get ready for some seriously unproductive (but fun) games.</CardDescription>
            </CardHeader>
            <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    {games.map((game) => (
                        <Card key={game.title} className="flex flex-col">
                            <CardHeader className="flex-row items-center gap-4">
                                <game.icon className="w-8 h-8 text-accent"/>
                                <div>
                                    <CardTitle className="text-lg">{game.title}</CardTitle>
                                    <CardDescription className="text-xs">{game.description}</CardDescription>
                                </div>
                            </CardHeader>
                            <CardContent className="flex-1 flex flex-col justify-end gap-4">
                               <Button onClick={() => setActiveGame(game.id)} disabled={!game.component}>
                                    {game.status}
                                </Button>
                            </CardContent>
                        </Card>
                    ))}
                </div>
            </CardContent>
        </Card>
    )
}
